body {
  margin: 0 !important;
}

.icon {
  width: 1em;
  height: 1em;
  stroke-width: 0;
  transition: stroke-width .5s ease-out;
  display: inline-block;
}

#start, #pause {
  fill: #000;
}

.icon:hover {
  stroke: #fff;
  stroke-width: 5px;
  stroke-opacity: 1;
  cursor: pointer;
  transition: all .3s;
  transform: scale(1.6);
}

.map {
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
}

.date {
  transition: all .5s ease-in-out;
}

.mainNav {
  text-align: center;
  background-color: #c34f4f;
