  border-bottom: 1px solid #751b1b;
  padding: 10px 0;
}

.additionalNav {
  background-color: #814fc3;
  border-top: 1px solid #2b1675;
  border-bottom: 1px solid #2f1b75;
  padding: 10px 0;
}

.sticky {
  width: 100%;
  z-index: 100;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.countryFlagActionCountry {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}

.countryFlagActionCountry:hover {
  filter: brightness(50%);
  transition: all .3s ease-in-out;
}

.countryPanelList {
  color: #fff;
  z-index: 200;
  background-color: #2b1675;
  border: 3px solid red;
  border-radius: 5%;
