}

.mainNav > ol {
  height: 35px;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 200%;
  list-style-type: none;
  display: inline-block;
}

.mainNav > ol a {
  color: #fff;
  text-decoration: none;
  display: block;
}

.mainNav > ol > li {
  float: left;
  width: 150px;
  height: 40px;
  border-right: 1px dashed #751b1b;
}

.mainNav > ol > li:first-child {
  border-left: 1px dashed #751b1b;
}

.mainNav > ol > li:hover {
  z-index: 100;
  background-color: #cf6969;
}

.mainNav > ol > li:hover > a {
  color: #451717;
}

.mainNav > ol > li > ul {
  height: 40px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}

.mainNav > ol > li:hover > ul {
  display: block;
}

.mainNav > ol > li > ul > li {
  z-index: 100;
  background-color: #cf6969;
  border-top: 1px dashed #751b1b;
  position: relative;
}

.mainNav > ol > li > ul > li:hover {
  background-color: #c34f4f;
}

.mainNav > ol > li > ul > li:hover > a {
  color: #451717;
}

.anChildLeft {
  color: #fff;
  padding-left: 50px;
  padding-right: 50px;
  display: inline-block;
}

.anChildLeftClose {
  color: #fff;
  padding-left: 5px;
